//页面调用接口方法
import axios from 'axios'
import qs from 'qs'

import {Indicator} from 'mint-ui';
import {consoledebug, judgeIsNullStr} from "./common";

//业务名
const BUSI_NAME = "shop";   //小艺商城

var DEBUG_FLAG = "true";
//调试标志符
if (window.localStorage.getItem("api_debug") && !judgeIsNullStr(window.localStorage.getItem("api_debug"))) {
  var DEBUG_FLAG = window.localStorage.getItem("api_debug");    //debug在首页已经加载完毕
}

consoledebug.log("DEBUG_FLAG:" + DEBUG_FLAG);

// axios 配置
let base = DEBUG_FLAG == "true" ? 'https://cs.yishubang.org/' : 'https://art.yishubang.org/'; //基础接口
consoledebug.log("base:" + base);

axios.defaults.retry = 4
axios.defaults.retryDelay = 1000
// axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.baseURL = base

//request拦截器
axios.interceptors.request.use((config) => {
  // consoledebug.log("axios.interceptors.request before config:" + JSON.stringify(config));   //调试用
  //统一配置busi_name、user_id和token
  if (config.method == "get") {
    //判断是否包含busi_name属性，如果包含该属性，则不再重新赋值
    if (!config.params.hasOwnProperty('busi_name')) {
      config.params.busi_name = BUSI_NAME;
    }
    //如果未传入user_id，则从localStorage中获取user_id，则token也带入
    if (!config.params.hasOwnProperty('user_id')) {
      if (window.localStorage.getItem("user_id") && !judgeIsNullStr(window.localStorage.getItem("user_id"))) {   //如果存在user_id
        config.params.user_id = window.localStorage.getItem("user_id");
      }
      if (window.localStorage.getItem("token") && !judgeIsNullStr(window.localStorage.getItem("token"))) {   //如果存在token
        config.params.token = window.localStorage.getItem("token");
      }
    }
  }
  if (config.method == "post") {
    //判断是否包含busi_name属性，如果包含该属性，则不再重新赋值
    if (!config.data.hasOwnProperty('busi_name')) {
      config.data.busi_name = BUSI_NAME;
    }
    //如果未传入user_id，则从localStorage中获取user_id，则token也带入
    if (!config.data.hasOwnProperty('user_id')) {
      if (window.localStorage.getItem("user_id") && !judgeIsNullStr(window.localStorage.getItem("user_id"))) {   //如果存在user_id
        config.data.user_id = window.localStorage.getItem("user_id");
      }
      if (window.localStorage.getItem("token") && !judgeIsNullStr(window.localStorage.getItem("token"))) {   //如果存在token
        config.data.token = window.localStorage.getItem("token");
      }
    }
    config.data = qs.stringify(config.data);
  }
  consoledebug.log("axios.interceptors.request after config:" + JSON.stringify(config));   //调试用
  return config;
}, (error) => {
  // consoledebug.log("axios.interceptors.request err:" + JSON.stringify(error));
  return Promise.reject(error);
});

//response拦截器
axios.interceptors.response.use((res) => {
  // consoledebug.log("res:" + JSON.stringify(res));    //调试用
  Indicator.close();
  return res;
}, (error) => {
  //404等问题可以在这里处理
  Indicator.close();
  return Promise.reject(error);
});

/*
 * axios ajax请求
 *
 * @params 参数
 * @url 链接
 * @method 方法
 * @success_callback
 * @error_callback
 *
 */
function axios_ajax(url, params, method, loadding_flag) {

  //请注意，配置param，包括放入user_id和token，应该在request拦截器中实现
  consoledebug.log("axios_ajax method:" + method + " url:" + url + "  params:" + JSON.stringify(params));
  //加载图标
  if (loadding_flag) {
    Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
    });
  }

  //GET方式
  if (method.toUpperCase() == "GET") {
    return axios({
      url: url,
      method: 'get',
      params: params
    })
  }

  //POST方式
  if (method.toUpperCase() == "POST") {
    return axios({
      url: url,
      method: 'post',
      data: params
    })
  }

}


//以下为接口调用的封装/////////////////////////////////////////////////////////////

//获取轮播图列表
export const ad_getListByCon = params => {
  return axios_ajax('api/shop/ad/getListByCon', params, 'GET', false);
}

//根据id获取轮播图信息
export const ad_getById = params => {
  return axios_ajax('api/shop/ad/getById', params, 'GET', true);
}

//获取商品列表
export const goods_getListByCon = params => {
  return axios_ajax('api/goods/getListByCon', params, 'GET', true);
}

//根据id获取商品详情
export const goods_getById = params => {
  return axios_ajax('api/goods/getById', params, 'GET', true);
}

//获取商品分类列表
export const goodsType_getListByCon = params => {
  return axios_ajax('api/goodsType/getListByCon', params, 'GET', false);
}

//获取作品列表
export const article_getListByCon = params => {
  return axios_ajax('api/article/getListByCon', params, 'GET', false);
}

//随机获取作品
export const article_getListByRand = params => {
  return axios_ajax('api/article/getListByRand', params, 'GET', true);
}

//根据id获取作品详情
export const article_getById = params => {
  return axios_ajax('api/article/getById', params, 'GET', true);
}

//获取微信配置接口
export const wechat_wxConfig = params => {
  params.debug = DEBUG_FLAG;    //配置调试标识
  // params.debug = true;    //测试用debug
  consoledebug.log("wechat_wxConfig params:" + JSON.stringify(params));
  return axios_ajax('api/isartfwh/wechat/wxConfig', params, 'POST', true);
}

//根据id获取用户信息
export const user_getById = params => {
  return axios_ajax('api/user/getById', params, 'GET', true);
}

//点赞接口
export const zan_setZan = params => {
  return axios_ajax('api/zan/setZan', params, 'POST', true);
}

//获取默认地址
export const address_getDefault = params => {
  return axios_ajax('api/address/getDefault', params, 'GET', true);
}

//设置地址
export const address_edit = params => {
  return axios_ajax('api/address/edit', params, 'POST', true);
}

//下单接口
export const order_payOrder = params => {
  return axios_ajax('api/shop/order/payOrder', params, 'POST', true);
}


//进行方法输出
export default {
  axios_ajax,   //基础axios方法封装
  ad_getListByCon,
  ad_getById,
  goods_getListByCon,
  goods_getById,
  goodsType_getListByCon,
  article_getListByCon,
  article_getListByRand,
  article_getById,
  wechat_wxConfig,
  zan_setZan,
  user_getById,
  address_getDefault,
  address_edit,
  order_payOrder
}





