//公共方法相关//////////////////////////////////////////////////////////////
export const showObject = (obj) => {
  var des = "";
  for (var name in obj) {
    des += name + ":" + obj[name] + ";";
  }
  return des;
}

//判断是否为空
/*
 * 其中val的值为null、""和undefined都算为空
 *
 */
export const judgeIsNullStr = (val) => {
  if (val == null || val == "" || val == undefined || val == "未设置") {
    return true
  }
  return false
}


//对象是否在数组中
/*
 * 传入对象的id，数组是json数组，其中id是对应的id
 *
 * By TerryQi
 *
 */
export const isDomInArr = (id, arr) => {
  for (var i = 0; i < arr.length; i++) {
    // consoledebug.log("isDomInArr id:" + id + " arr_id:" + arr[i].id)
    if (id == arr[i].id) {
      return true;
    }
  }
  return false;
}

/*
 * 用于对象克隆
 *
 * obj 对象，返回克隆对象
 *
 */
export const clone = (obj) => {
  // Handle the 3 simple types, and null or undefined
  if (null == obj || "object" != typeof obj) return obj;

  // Handle Date
  if (obj instanceof Date) {
    var copy = new Date();
    copy.setTime(obj.getTime());
    return copy;
  }

  // Handle Array
  if (obj instanceof Array) {
    var copy = [];
    for (var i = 0, len = obj.length; i < len; ++i) {
      copy[i] = clone(obj[i]);
    }
    return copy;
  }

  // Handle Object
  if (obj instanceof Object) {
    var copy = {};
    for (var attr in obj) {
      if (obj.hasOwnProperty(attr)) copy[attr] = clone(obj[attr]);
    }
    return copy;
  }

  throw new Error("Unable to copy obj! Its type isn't supported.");
}

