import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

let routers = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '/index',     //首页商品列表
    name: 'index',
    component: resolve => require(['@/views/index'], resolve),
    meta: {
      keepAlive: true // true 表示需要使用缓存
    }
  },
  {
    path: '/circle',      //朋友圈
    name: 'circle',
    component: resolve => require(['@/views/circle'], resolve),
    meta: {
      keepAlive: true // true 表示需要使用缓存
    }
  },
  {
    path: '/mine',      //我的页面
    name: 'mine',
    component: resolve => require(['@/views/mine'], resolve),
    meta: {
      keepAlive: true // true 表示需要使用缓存
    }
  },
  {
    path: '/goods/info/:goods_id',      //商品信息页面
    name: 'goods',
    component: resolve => require(['@/views/goods/goods'], resolve),
    meta: {
      keepAlive: false // false 表示需要使用缓存
    }
  },
  {
    path: '/article/info/:article_id',      //作品信息页面
    name: 'article',
    component: resolve => require(['@/views/article/article'], resolve),
    meta: {
      keepAlive: false // false 表示需要使用缓存
    }
  },
  {
    path: '/goods/payOrder/:goods_id',      //下单支付页面
    name: 'payOrder',
    component: resolve => require(['@/views/goods/pay/payOrder'], resolve),
    meta: {
      keepAlive: false // false 表示需要使用缓存
    },
  },
  {
    path: '/goods/paySuccess/:goods_id',      //支付成功
    name: 'paySuccess',
    component: resolve => require(['@/views/goods/pay/paySuccess'], resolve),
    meta: {
      keepAlive: false // false 表示需要使用缓存
    },
  },
];

export default new Router({
  mode: 'hash',  // 默认hash
  routes: routers,    //统一管理路由
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      if (from.meta.keepAlive) {
        from.meta.savedPosition = document.body.scrollTop
      }
      return {x: 0, y: to.meta.savedPosition || 0}
    }
  }
})



